body{
    margin: 0;
    padding: 0;
}

.resource-card{
    position: relative;
}
.resource-card a{
    color: gray;
    text-decoration: none;
}
.resource-card a:hover{
    color: black;
    text-decoration: none;
    box-shadow: 2px 1px 10px rgba(0, 0, 0, 0.2);
}
.custom-card{
    width: 280px;
    height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #eee;
    margin: 10px;
    color: gray;
    text-decoration: none;
}

.custom-card:hover{
    color: black;
    text-decoration: none;
    box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.5);
}
.action-btns{
    display: none;
}
.action-btns > span{
    padding: 10px;
    cursor: pointer;
}
.resource-card:hover .difficulty-badge{
    display: none;
}
.resource-card:hover .action-btns{
    display: block;
    position: absolute;
    top: 10px;
    right: 10px;
}
.CircularProgressbar {
    width: 100px;
    height: auto;
}

.add-resource-btn{
    width: 60px;
    height: 60px;
    background-color: rebeccapurple;
    color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 20px;
    bottom: 20px;
    cursor: pointer;
}